import { Neo4jInstrumentationConfig } from 'opentelemetry-instrumentation-neo4j';
import { AspectoAttributeNames } from '@aspecto/opentelemetry-base';
import { callCapturePayloadHook } from './recording-span';

export const aspectoNeo4jInstrumentationConfig: Neo4jInstrumentationConfig = {
    ignoreOrphanedSpans: true,
    moduleVersionAttributeName: AspectoAttributeNames.MODULE_VERSION,
    responseHook: callCapturePayloadHook((span, response) => {
        span.setAttribute(AspectoAttributeNames.DB_RESPONSE, JSON.stringify(response.records.map((r) => r.toObject())));
    }),
};
